(function() {
  'use strict';

  angular
    .module('vizbox', ['ngAnimate', 'ngSanitize', 'ngMessages', 'ngAria', 'ngRoute', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('vizbox')
    .directive('pieChart', pieChart);

    /** @ngInject */

    function pieChart() {
      var directive = {
          restrict: 'E',
          replace: true,
          scope: { rows : '=rows', chartId : '=chartId'},
          link: function(scope,element,attrs) {

            var template = '<div id="'+scope.chartId+'"></div>';
            element.html(template);

            var chart = c3.generate({
                            bindto: '#' + scope.chartId,
                            data: {
                              columns: scope.rows,
                              type:'pie',
                              colors: {
                                d1:'#73C2FB',
                                d2:'#1F75FE',
                                d3:'#0247FE',
                                d4:'#4F86F7'
                              }
                            }
                        });

            scope.$watch('rows',function() {
                 chart.load({columns: scope.rows});
            });

          }
      };

      return directive;
    }

})();

(function() {
  'use strict';

  angular
    .module('vizbox')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('vizbox')
    .directive('lineChart', lineChart);

    /** @ngInject */

    function lineChart() {
      var directive = {
          restrict: 'E',
          replace: true,
          scope: { rows : '=rows', chartId : '=chartId'},
          link: function(scope,element,attrs) {

            var template = '<div id="'+scope.chartId+'"></div>';
            element.html(template);

            var chart = c3.generate({
                            bindto: '#' + scope.chartId,
                            padding: {
                              top:10,
                              right:20
                            },
                            data: {
                              x: 'x',
                              columns: scope.rows,
                              colors: {
                                d1:'#0247FE',
                                d2:'#73C2FB'
                              }
                            },

                            axis: {
                                x: {
                                  type:'indexed',
                                  tick: {
                                    fit: false
                                  }
                                },
                                y: {
                                    max: 80,
                                    min: 0,
                                    padding: {
                                        top: 0,
                                        bottom:0
                                    },
                                    tick: {
                                      outer:false,
                                      count:5,
                                      fit:true
                                    }
                                }

                            },
                            grid: {
                               x: {
                                   show: true
                               },
                               y: {
                                   show: true
                               }
                           }
                        });

            scope.$watch('rows',function() {
                chart.load({columns:scope.rows,duration:1500});
            });

          }
      };

      return directive;
    }

})();

