(function() {
  'use strict';

  angular
    .module('vizbox')
    .directive('barChart', barChart);

    /** @ngInject */

    function barChart() {
      var directive = {
                  restrict: 'E',
                  replace: false,
                  scope: { rows : '=rows', chartId : '=chartId'},
                  link: function(scope,element,attrs) {

                    var template = '<div id="'+scope.chartId+'"></div>';
                    element.html(template);

                    var months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
                    var colors = ['#5cd3ff','#47ceff','#33c9ff','#1fc3ff','#00bbff','#00b4f5','#00a5e0','#0096cc','#0087b8','#0078a3','#00698f', '#005a7a'];
                    var chart = c3.generate({
                                  bindto: '#' + scope.chartId,
                                  data: {
                                    type:'bar',
                                    columns: scope.rows,
                                    colors: {
                                      d1: function(d) {
                                        return colors[d.index];
                                      }
                                    }
                                  },
                                  bar: {
                                    width: {
                                        ratio: 0.8
                                    }
                                  },
                                  axis: {
                                      y: {
                                          max:60,
                                          min: 0,
                                          padding: {top:0, bottom:0},
                                          tick: {
                                            format: function(d) {
                                              return d + 'k'
                                            }
                                          }
                                      },
                                      x: {
                                        type: 'category',
                                        tick: {
                                          format: function(d) {
                                            console.log('d',d);
                                            return months[d].slice(0, d !== 8 ? 3 : 4);
                                          }
                                        }
                                      }
                                  },
                                  legend: {
                                    show: false
                                  },
                                  padding: {
                                    top:10,
                                    right:20
                                  },
                                  grid: {
                                    y: {
                                        show: true
                                    }
                                  },
                                  tooltip: {
                                    format: {
                                      title: function(d) {
                                        return months[d];
                                      },
                                      value: function (x) {
                                        return '$ '+ d3.format(',')((1000 * x).toFixed(2));
                                      }
                                    }
                                  }
                              });

                    scope.$watch('rows',function() {
                      chart.load({columns: scope.rows});
                    });
              }
      };

      return directive;
    }

})();

(function() {
  'use strict';

  angular
    .module('vizbox')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, $http, $interval,geoLocation) {
      $scope.geoChartId = 'geo';
      $scope.geoLocation = geoLocation;





      $scope.pieChartId = 'pieChart';
      var pieData = new pieDataCreator();
      pieData.setLength(12);
      pieData.setAverageAndVariance('d1',50,30);
      pieData.setAverageAndVariance('d2',25,20);
      pieData.setAverageAndVariance('d3',80,20);
      pieData.setAverageAndVariance('d4',25,5);
      $scope.pieRows = pieData.getData();

      $scope.pieInterval = $interval(function() {
        $scope.pieRows = pieData.getData();
      }, 2000);

      function pieDataCreator() {
        return {
          length: 0,
          data: {
            d1: {
              array: [],
              average: 0,
              variance: 0
            },
            d2: {
              array: [],
              average: 0,
              variance: 0
            },
            d3: {
              array: [],
              average: 0,
              variance: 0
            },
            d4: {
              array: [],
              average: 0,
              variance: 0
            }
          },
          setAverageAndVariance: function(name, average, variance) {
            this.data[name]['average'] = average;
            this.data[name]['variance'] = variance;
          },
          setLength: function(length) {
            this.length = length;
          },
          getData: function() {
            var _this = this;
            return Object.keys(_this.data).map(function(key) {
              var array = [];
              array.push(key);
              var length = _this.length;
              var average = _this.data[key].average;
              var variance = _this.data[key].variance;
              for (var i =0; i < length; i++) {
                array.push(_this.getNumber(average, variance));
              }
              return array;
            })
          },
          getNumber: function(average, variance) {
            return average + (0.5 - Math.random()* variance);
          }
        }
      }

      $scope.barChartId = 'barChart';

      $scope.barRows = $scope.pieRows.slice(0,1);

        console.log('$scope.pieData',$scope.barRows);

      //lineChart
      $scope.lineChartId = 'lineChart';
      var lineData = new lineDataCreator();
      lineData.setLength(12);
      lineData.setAverageAndVariance('d1Data',55,15);
      lineData.setAverageAndVariance('d2Data',30,20);

      $scope.lineRows =  lineData.getInitialArray();
      $scope.lineInterval = $interval(function() {
             $scope.lineRows = lineData.getUpdatedArray();
      }, 5000);

      function lineDataCreator() {
        return {
          length: 0,
          data: {
            xData: {
              array: ['x'],
              average: 0,
              variance: 0
            },
            d1Data: {
              array: ['d1'],
              average: 0,
              variance: 0
            },
            d2Data: {
              array: ['d2'],
              average: 0,
              variance: 0
            }
          },
          setAverageAndVariance: function(name, average, variance) {
            this.data[name]['average'] = average;
            this.data[name]['variance'] = variance;
          },
          setLength: function(length) {
            this.length = length;
          },
          getInitialArray: function(number) {
            var _this = this;
            return Object.keys(_this.data).map(function(key) {
              var array = _this.data[key].array;
              var length = _this.length;
              var average = _this.data[key].average;
              var variance = _this.data[key].variance;
              for (var i =0; i < length; i++) {
                if (key === 'xData') {
                    array.push(i);
                } else {
                    array.push(_this.getNumber(average, variance));
                  }
              }
              return array;
            })
          },
          getUpdatedArray: function() {
            var _this = this;
            return Object.keys(this.data).map(function(key) {
              var array = _this.data[key].array;
              var length = _this.length;
              var average = _this.data[key].average;
              var variance = _this.data[key].variance;
              array.splice(1,1);
              if (key === 'xData') {
                  array.push(+array[array.length-1] + 1);
              } else {
                  array.push(_this.getNumber(average, variance))
              }
              return array;

            })
          },
          getNumber: function(average, variance) {
            return average + (0.5 - Math.random()* variance);
          }
        }
      }
  }
})();

